export default {
  data() {
    return {
      warningText: null,
    };
  },
  props: {
    id: {
      type: String,
    },
    label: {
      type: String,
      required: true,
    },
    value: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    type: {
      type: String,
      default: "text",
    },
    warning: {
      type: String,
      default: null,
    },
  },
  methods: {
    select() {
      this.warningText = null;
    },
  },
  watch: {
    warning() {
      this.warningText = this.warning;
    },
  },
};
