export default {
  data() {
    return {
      isOpen: false,
      isRemoved: false,
    };
  },
  props: {
    title: String,
    address: String,
    fullname: String,
    job: String,
    email: String,
    phone: String,
    remove: Function,
    editOffice: Function
  },
  methods: {
    dropdown() {
      this.isOpen = !this.isOpen;
    },
    autoRemove(){
      this.isOpen = false;
      this.isRemoved = true;
      setTimeout(()=>this.remove(), 500);
    },
    autoEdit(){
      this.dropdown();
      this.editOffice();
    }
  }
};
