import OfficeCard from "../components/officeCard.vue";
import LocationCard from "../components/locationCard.vue";
import offices from "../data/fake-data.json";

export default {
  data() {
    return {
      offices,
      isUpdated: false,
      isLocationCardOpen: false,
      isEdit: false,
    };
  },
  components: {
    OfficeCard,
    LocationCard,
  },
  methods: {
    save(data, index) {
      if (index) {
        const offices = [...this.offices];
        offices[index] = {
          title: data.title,
          address: data.address,
          fullname: data.fullname,
          job: data.job,
          email: data.email,
          phone: data.phone,
        };
        this.offices = offices;
      } else
        this.offices.push({
          title: data.title,
          address: data.address,
          fullname: data.fullname,
          job: data.job,
          email: data.email,
          phone: data.phone,
        });
    },
    remove(index) {
      this.$delete(this.offices, index);
    },
    editOffice(index) {
      window.scrollTo({ top: 0, behavior: "smooth" });
      this.isEdit = true;
      this.$refs.locationcard.open(this.offices[index], index);
    },
  },
  watch: {
    offices() {
      window.scrollTo({ top: 0, behavior: "smooth" });
      this.isUpdated = true;
      setTimeout(() => (this.isUpdated = false), 4000);
    },
  },
};
